import { Component, OnInit, ViewEncapsulation } from '@angular/core';

import { OrganizationService } from 'modules/organization/organization.service';
import { TreeviewItem, TreeviewConfig } from 'ngx-treeview';
import { Name } from 'modules/user/profile/profile.service';

@Component({
  selector: 'app-organization-tree',
  templateUrl: './organization-tree.component.html',
  styleUrls: ['./organization-tree.component.scss'],
  providers: [ OrganizationService ],
  encapsulation: ViewEncapsulation.None
})
export class OrganizationTreeComponent implements OnInit {

  employeeTree: Array<TreeviewItem>;

  treeConfig = TreeviewConfig.create({
    hasAllCheckBox: false,
    hasFilter: false,
    hasCollapseExpand: false,
    decoupleChildFromParent: false
  });

  constructor(
    private organizationService: OrganizationService
  ) { }

  ngOnInit() {
    this.__loadTree();
  }

  private __populateTreeItems(items) {

    const tree = new Array<TreeviewItem>();
    for (const item of items) {

      const text = Name.getFullName(item.name) + ', ' + item.designation;
      const treeitem = new TreeviewItem({
        text: text,
        value: item._id
      });

      if (item.subordinates) {
        treeitem.collapsed = true;
        treeitem.children = this.__populateTreeItems(item.subordinates);
      }
      tree.push(treeitem);
    }
    return tree;
  }

  private __loadTree() {

    this.organizationService.getTree()
      .then(response => {

        this.employeeTree = this.__populateTreeItems(response.tree);
      });
  }
}
